"use client";
import Image from "next/image";
import { useState } from "react";

export default function MultiStepForm() {
  const totalSteps = 3;
  const [step, setStep] = useState(1); // глобальний крок
  const [selected, setSelected] = useState<string | null>(null);
  const [date, setDate] = useState("2025-10-20");
  const [message, setMessage] = useState("");

  const options = [
    "Friends first",
    "Casual dating",
    "Serious relationships",
    "Activity partner",
  ];

  // вкладені кроки
  const [subStep, setSubStep] = useState(0); // 0 = неактивні
  const totalSubSteps = 3;

  const handleClick = (option: string) => {
    setSelected(option);
    if (option === "Friends first") {
      setSubStep(1); // стартуємо вкладені кроки
    } else {
      setSubStep(0); // інші опції без підкроків
    }
  };

  const nextStep = () => {
    if (subStep > 0 && subStep < totalSubSteps) {
      setSubStep((prev) => prev + 1);
    } else if (subStep === totalSubSteps) {
      setSubStep(0);
      setStep((prev) => Math.min(prev + 1, totalSteps));
    } else {
      setStep((prev) => Math.min(prev + 1, totalSteps));
    }
  };

  const prevStep = () => {
    if (subStep > 1) {
      setSubStep((prev) => prev - 1);
    } else if (subStep === 1) {
      setSubStep(0); // виходимо з підкроків
    } else {
      setStep((prev) => Math.max(prev - 1, 1));
    }
  };

  const progressPercentage =
    subStep > 0 ? (subStep / totalSubSteps) * 100 : (step / totalSteps) * 100;

  return (
    <section className="grid items-center justify-items-center sm:pt-[64px] pt-[30px] px-4 gap-16 font-[family-name:var(--font-geist-sans)]">
      <Image src="/Logo.svg" alt="Dating logo" width={48} height={88} />

      <div className="w-full max-w-[596px]">
        <button onClick={prevStep} className="mb-4 flex items-center gap-2">
          <span className="transform">&lt;-</span> Back
        </button>
        <div className="h-[4px] bg-[#D1D1D1] rounded-full mb-6">
          <div
            className="h-[4px] bg-[#1677FF] rounded-full transition-all duration-300"
            style={{ width: `${progressPercentage}%` }}></div>
        </div>
      </div>

      {/* Контент */}
      <div className="max-w-[596px] mx-auto py-8 sm:bg-[#F9F9F9] sm:shadow-[0px_2px_8px_0px_#0000001F] rounded-lg w-full">
        {/* Step 1 */}
        {step === 1 && subStep === 0 && (
          <div className="flex flex-col gap-4 max-w-[400px] mx-auto sm:mb-6 mb-40">
            <h2 className="text-xl font-semibold">What are you looking for?</h2>
            <div className="flex gap-3 flex-wrap">
              {options.map((option) => (
                <button
                  key={option}
                  onClick={() => handleClick(option)}
                  className={`px-4 py-2 rounded-lg transition-colors duration-200
                    ${
                      selected === option
                        ? "bg-[#1677FF] text-[#fff] border-blue-500"
                        : "bg-[#fff] text-black hover:bg-gray-100"
                    }`}>
                  {option}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Підкроки */}
        {step === 1 && subStep > 0 && (
          <div className="flex flex-col gap-4 max-w-[400px] mx-auto">
            <h2 className="text-xl font-semibold">
              Friends first — Step {subStep}
            </h2>
            {subStep === 1 && <p>Question 1 about friendship…</p>}
            {subStep === 2 && <p>Question 2 about friendship…</p>}
            {subStep === 3 && <p>Question 3 about friendship…</p>}
          </div>
        )}

        {/* Step 2 */}
        {step === 2 && (
          <div className="flex flex-col gap-4 max-w-[400px] mx-auto">
            <h2 className="text-xl font-semibold text-center">
              Where do you live?
            </h2>
            <div className="flex flex-col gap-3">
              {/* Випадаючий список */}
              <label htmlFor="" className="font-semibold">
                Country
              </label>
              <select
                name="country"
                className="border p-2 rounded-[8px] border-[#DCDCDC] focus:outline-none"
                defaultValue="">
                <option value="" disabled>
                  Select country
                </option>
                <option value="ua">Ukraine</option>
                <option value="pl">Poland</option>
                <option value="de">Germany</option>
              </select>

              <label htmlFor="" className="font-semibold">
                City
              </label>
              {/* Інпут для міста */}
              <input
                type="text"
                name="city"
                placeholder="City"
                className="border p-2 rounded-[8px] placeholder:text-[#000] border-[#DCDCDC] focus:outline-none md:mb-4 mb-40"
              />
            </div>
          </div>
        )}

        {/* Step 3 */}
        {step === 3 && (
          <div className="flex flex-col gap-4 max-w-[400px] mx-auto">
            <h2 className="text-xl font-semibold text-center">
              Tell us about yourself
            </h2>

            <label htmlFor="" className="font-semibold">
              Date of birth
            </label>
            <input
              type="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              className="w-full border p-2 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 "
            />
            <div className="md:mb-4 mb-20">
              <label htmlFor="" className="font-semibold">
              Biography
            </label>
              <textarea
                name="message"
                placeholder="Enter"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                rows={4}
                className="w-full border p-2 rounded resize-none focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>
        )}

        {/* Кнопка Continue/Submit */}
        <button
          onClick={nextStep}
          className=" bg-[#1677FF] hover:bg-[#2b7ff5] text-[#fff] rounded-[10px] text-base py-[9px] px-[16px] w-full h-10 flex justify-center items-center gap-2">
          {step === totalSteps ? "Submit" : "Continue"} <span>-&gt;</span>
        </button>
      </div>
    </section>
  );
}
